import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import logo from "./logo.svg";

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 500,
    margin: 'auto',
    backgroundColor: theme.palette.background.paper,
  },
  "@keyframes rotate": {
    'from': {
      transform: 'rotate(0deg)'
    },
    'to': {
      transform: 'rotate(360deg)'
    }
  },
  logo: {
    animation: "$rotate infinite 20s linear",
  }
}));

function ListItemLink(props: any) {
  return <ListItem button component="a" {...props} />;
}

type Github_link = {
  link: string;
  react: boolean;
}

export const SimpleList: React.FC<{ links: Github_link[]}> = ({ links }) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <List component="nav" aria-label="main mailbox folders">
        {links.map((link: Github_link) => {
          const arr = link.link.split('/');
          const label = arr[arr.length - 1];
          return <ListItemLink href={link.link} style={{ textDecoration: 'none'}}>
            <ListItemText primary={label.toLocaleUpperCase()} />
            {link.react && <img src={logo} width={50} height={50} alt="img" className={classes.logo} />}
            </ListItemLink>
        })}

      </List>
    </div>
  );
}
